import Link from 'next/link';

const Navbar = () => (

		<div>
        <ul>
			<li> <Link href="/"> Home </Link> </li>

			<li> <Link href="#">
  <a>
    <div>Foo</div>
    <div>Bar</div>
  </a>
</Link> </li>
			
		</ul>

		<style jsx>{`

			ul {
				paddding:0;
				display:flex;
				background: #000;
				color:#fff;
			}

			ul li {
				list-style:none;

			}
			ul li a {
				color:#fff;
				text-decoration:none;
			}
		
		`}</style>

		
		</div>

); 

export default Navbar;


